.container{
    margin: 16px auto;
    padding: 15px;
    background-color: #464646;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 60vw;
}

.container .data{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    margin-top: 10px;
}

.input-container{
    width: 100%;
}

@media ( min-width: 600px ) {
    .container .data{
        flex-direction: row;
    }
    
    .input-container{
        width: 45%;
    }
}

@media ( max-width: 1000px ) {
    .container{
        max-width: 90vw;
    }
}